import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin } from '@/lib/supabase/server'
import { v4 as uuidv4 } from 'uuid'
import type { WorkflowFolder } from '@/lib/accelerator/types'

// ==========================================
// GET /api/v1/workflows/folders - List workflow folders
// ==========================================
export async function GET(request: NextRequest) {
  try {
    const supabase = supabaseAdmin
    const { searchParams } = new URL(request.url)
    
    const subAccountId = searchParams.get('subAccountId')
    const folderId = searchParams.get('id')
    const parentFolderId = searchParams.get('parentFolderId')

    if (!subAccountId) {
      return NextResponse.json(
        { success: false, error: 'subAccountId is required' },
        { status: 400 }
      )
    }

    // If specific folder requested
    if (folderId) {
      const { data: folder, error } = await supabase
        .from('workflow_folders')
        .select('*')
        .eq('id', folderId)
        .eq('sub_account_id', subAccountId)
        .single()

      if (error || !folder) {
        return NextResponse.json(
          { success: false, error: 'Folder not found' },
          { status: 404 }
        )
      }

      return NextResponse.json({
        success: true,
        data: transformFolder(folder)
      })
    }

    // List all folders
    let query = supabase
      .from('workflow_folders')
      .select('*')
      .eq('sub_account_id', subAccountId)
      .order('name', { ascending: true })

    if (parentFolderId === 'null' || parentFolderId === '') {
      query = query.is('parent_folder_id', null)
    } else if (parentFolderId) {
      query = query.eq('parent_folder_id', parentFolderId)
    }

    const { data: folders, error } = await query

    if (error) {
      console.error('Error fetching folders:', error)
      return NextResponse.json(
        { success: false, error: 'Failed to fetch folders' },
        { status: 500 }
      )
    }

    const transformedFolders = (folders || []).map(transformFolder)

    return NextResponse.json({
      success: true,
      data: transformedFolders
    })

  } catch (error) {
    console.error('Error in GET /api/v1/workflows/folders:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// POST /api/v1/workflows/folders - Create folder
// ==========================================
export async function POST(request: NextRequest) {
  try {
    const supabase = supabaseAdmin
    const body = await request.json()

    const {
      subAccountId,
      name,
      description,
      color,
      parentFolderId
    } = body

    if (!subAccountId || !name) {
      return NextResponse.json(
        { success: false, error: 'subAccountId and name are required' },
        { status: 400 }
      )
    }

    const folderId = uuidv4()
    const { data: folder, error } = await supabase
      .from('workflow_folders')
      .insert({
        id: folderId,
        sub_account_id: subAccountId,
        name,
        description,
        color: color || '#6366f1',
        parent_folder_id: parentFolderId || null,
        workflow_count: 0
      })
      .select()
      .single()

    if (error) {
      console.error('Error creating folder:', error)
      return NextResponse.json(
        { success: false, error: 'Failed to create folder' },
        { status: 500 }
      )
    }

    return NextResponse.json({
      success: true,
      data: transformFolder(folder)
    }, { status: 201 })

  } catch (error) {
    console.error('Error in POST /api/v1/workflows/folders:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// PUT /api/v1/workflows/folders - Update folder
// ==========================================
export async function PUT(request: NextRequest) {
  try {
    const supabase = supabaseAdmin
    const body = await request.json()

    const {
      id,
      subAccountId,
      name,
      description,
      color,
      parentFolderId
    } = body

    if (!id || !subAccountId) {
      return NextResponse.json(
        { success: false, error: 'id and subAccountId are required' },
        { status: 400 }
      )
    }

    // Build update object
    const updates: Record<string, any> = {
      updated_at: new Date().toISOString()
    }

    if (name !== undefined) updates.name = name
    if (description !== undefined) updates.description = description
    if (color !== undefined) updates.color = color
    if (parentFolderId !== undefined) updates.parent_folder_id = parentFolderId

    const { data: folder, error } = await supabase
      .from('workflow_folders')
      .update(updates)
      .eq('id', id)
      .eq('sub_account_id', subAccountId)
      .select()
      .single()

    if (error || !folder) {
      console.error('Error updating folder:', error)
      return NextResponse.json(
        { success: false, error: 'Failed to update folder' },
        { status: 500 }
      )
    }

    return NextResponse.json({
      success: true,
      data: transformFolder(folder)
    })

  } catch (error) {
    console.error('Error in PUT /api/v1/workflows/folders:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// DELETE /api/v1/workflows/folders - Delete folder
// ==========================================
export async function DELETE(request: NextRequest) {
  try {
    const supabase = supabaseAdmin
    const { searchParams } = new URL(request.url)
    
    const id = searchParams.get('id')
    const subAccountId = searchParams.get('subAccountId')
    const moveWorkflowsTo = searchParams.get('moveWorkflowsTo')

    if (!id || !subAccountId) {
      return NextResponse.json(
        { success: false, error: 'id and subAccountId are required' },
        { status: 400 }
      )
    }

    // Check if folder has workflows
    const { data: folder } = await supabase
      .from('workflow_folders')
      .select('workflow_count')
      .eq('id', id)
      .single()

    if (folder && folder.workflow_count > 0) {
      // Move workflows to another folder or unassign
      await supabase
        .from('workflows')
        .update({ folder_id: moveWorkflowsTo || null })
        .eq('folder_id', id)
    }

    // Delete the folder
    const { error } = await supabase
      .from('workflow_folders')
      .delete()
      .eq('id', id)
      .eq('sub_account_id', subAccountId)

    if (error) {
      console.error('Error deleting folder:', error)
      return NextResponse.json(
        { success: false, error: 'Failed to delete folder' },
        { status: 500 }
      )
    }

    return NextResponse.json({ success: true }, { status: 200 })

  } catch (error) {
    console.error('Error in DELETE /api/v1/workflows/folders:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// HELPER FUNCTIONS
// ==========================================

function transformFolder(dbFolder: any): WorkflowFolder {
  return {
    id: dbFolder.id,
    subAccountId: dbFolder.sub_account_id,
    name: dbFolder.name,
    description: dbFolder.description,
    color: dbFolder.color,
    parentFolderId: dbFolder.parent_folder_id,
    workflowCount: dbFolder.workflow_count,
    createdAt: dbFolder.created_at,
    updatedAt: dbFolder.updated_at
  }
}
